                  clickItem.typeList.length < 20
                "
                @click="plusOption(clickItem.typeList)"
              />
              <a-icon
                type="minus-circle"
                style="color: red; font-size: 14px; cursor: pointer"
                v-if="clickItem.typeList.length !== 1"
