<script>
import draggable from "vuedraggable";
import components from "./componentList";
import formulaModal from "./components/formulaModal";

export default {
  props: {
    defaultComponentList: {
      type: Array,
      default: () => [],
      required: false,
    },
    process: {
      type: Object,
      default: () => {
        return {};
      },
      required: false,
    },
  },
  components: {
    draggable,
    formulaModal,
  },
  watch: {
    componentList: {
      handler(val, oldVal) {
        console.log("componentList", val);
        this.$emit("change", val);
      },
      deep: true,
    },
  },
  data() {
    return {
      activeTab: "1",
      components: components.components, // 
      componentsName: components.componentsName, // 
      componentList: [], // 
      count: "componentId", // yid
      clickItemId: null, // id
      formulaModalShow: false, // 
      leaveTypeColumn: [
        { title: "", dataIndex: "type", width: "50%" },
        { title: "", dataIndex: "time", width: "50%" },
      ], // 
      leaveTypeData: [
        { type: "", time: "" },
        { type: "", time: "" },
        { type: "", time: "" },
        { type: "", time: "" },
      ], //
    };
  },
  created() {
    this.componentList = this.defaultComponentList;
  },
  computed: {
    clickItem() {
      const targetObj = (this.componentList || []).find((item) => {
        return item.id === this.clickItemId;
      });
      return targetObj || { name: "" };
    },
    toolList() {
      const toolList = this.componentList.filter((item) => {
        return item.type === "money" || item.type === "number";
      });
      return toolList || [];
    },
  },
  methods: {
    // 
    getcomponentName(item) {
      const clickItemType = item.type;
      const name = this.componentsName[clickItemType];
      return name;
    },
    // 
    dragChange(evt) {
      console.log(evt);
      if (evt.added) {
        this.count += 1;
        const item = evt.added.element;
        const idx = this.componentList.findIndex((e) => e.id === item.id);
        let temp = JSON.parse(JSON.stringify(this.componentList));
        temp[idx].id = this.count;
        this.componentList = temp;
      }
      console.log(this.componentList);
    },
    // 
    deleteItem(index, item) {
      console.log(item, this.componentList, this.process);
      console.log(
        "",
        this.canDelete(item, this.process)
      );
      // if(this.canDelete(item, this.process)) {

      // } else {
      //   this.$message.error('')
      // }
      this.componentList.splice(index, 1);
      this.clickItemId = null;
      // 
      if (item.type === "money" || item.type === "number") {
        this.componentList = this.componentList.map((component) => {
          if (component.type === "formula") {
            let flag = true;
            component.formulaList.forEach((formula) => {
              if (formula.id === item.id) {
                flag = false;
              }
            });
            if (flag) {
              return component;
            } else {
              return {
                ...component,
                formulaList: [],
              };
            }
          } else {
            return component;
          }
        });
      }
    },
    // 
    canDelete(targetObj, process) {
      // console.log('',targetObj, process);
      let flag = true;
      if (process && process.conditions) {
        process.conditions.forEach((item) => {
          (item.content || []).forEach((conditionItem) => {
            if (conditionItem.id === targetObj.id) {
              flag = false;
            }
          });
          if (item.childNode) {
            if (!this.canDelete(targetObj, item.childNode)) {
              flag = false;
            }
          }
        });
      }
      if (process && process.childNode) {
        if (!this.canDelete(targetObj, process.childNode)) {
          flag = false;
        }
      }
      return flag;
    },
    // 
    clickComponent(target) {
      console.log("click target", target);
      if (this.clickItemId === target.id) {
        this.clickItemId = null;
      } else {
        this.clickItemId = target.id;
      }
      console.log(this.clickItemId);
    },
    // 
    changeComponentE(name, e) {
      console.log(name, e.target.value, this.clickItemId, this.componentList);
      this.componentList = this.componentList.map((item) => {
        if (item.id === this.clickItemId) {
          let newItem = item;
          if (e.target.type === "checkbox") {
            newItem[name] = e.target.checked;
          } else {
            newItem[name] = e.target.value;
          }
          return newItem;
        } else {
          return item;
        }
      });
      console.log(this.componentList);
    },
    // 
    changeComponentFormula(formulaList) {
      console.log(formulaList, "");
      this.componentList = this.componentList.map((item) => {
        if (item.id === this.clickItemId) {
          let newItem = item;
          newItem.formulaList = formulaList;
          return newItem;
        } else {
          return item;
        }
      });
      this.formulaModalShow = false;
      console.log(this.componentList, "");
    },
    // 
    plusOption(optionList) {
      if (optionList.length < 20) {
        optionList.push({ name: `${optionList.length + 1}` });
      }
    },
    // 
    deleteOption(optionList, index) {
      optionList.splice(index, 1);
    },
    // 
    hasGroupComponent() {
      console.log('jjjjjjjjjjjjjj')
      let flag = false;
      this.componentList.forEach((item) => {
        if (item.category === "attendance") {
          flag = true;
        }
      });
      return flag;
    },
    // 
    save() {
      console.log(this.componentList, JSON.stringify(this.componentList));
    },
  },
};
</script>
